// Function to retrieve from local storage
export const getItem = (localStorage) => {
  if (localStorage) {
    const storedItems = localStorage.getItem('items');
    console.log('Retrieved items:', storedItems);
    return JSON.parse(storedItems) || [];
  }
  return [];
};
